.me {
  cursor: pointer;
}
.me h1 {
  color: #101011;
  letter-spacing: -0.1em;
  line-height: 0.8;
  position: absolute;
  top: 52vw;
  right: 0;
  width: 9em;
  font-size: 3rem;
  text-align: right;
  text-transform: uppercase;
  border-right: 0.4em solid #101011;
  padding-right: 0.2em;
  opacity: 1;
}
.me h1 span {
  font-size: 1.5em;
}
@media screen and (max-width: 1200px) {
  .me h1 {
    font-size: 3rem;
    top: 32vw;
  }
}
@media print {
  .me h1 {
    font-size: 3rem;
    top: 32vw;
  }
}

.line {
  background: url(../images/line.png);
  background-size: 100% 100%;
  width: 100%;
  height: 13rem;
  margin-top: -7rem;
  position: absolute;
  z-index: 99;
}

#gl {
  width: 100%;
  height: 67vw;
  overflow: hidden;
}
@media screen {
  #gl canvas {
    display: block;
  }
}
@media print {
  #gl {
    width: 210mm;
    height: 297mm;
    background: url(../images/me.jpg) center center;
    background-size: auto 100%;
  }
  #gl canvas {
    display: none;
  }
}