* {
  box-sizing: border-box;
}

@page {
  size: A4;
  margin: 0;
}
body, html {
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-family: furamono;
}
@media screen and (max-width: 700px) {
  body, html {
    font-size: 8px;
  }
}
@media screen and (max-width: 350px) {
  body, html {
    font-size: 6px;
  }
}
@media print {
  body, html {
    font-size: 2mm;
    width: 210mm;
    height: 297mm;
  }
}

@font-face {
  font-family: furamono;
  src: url(./fonts/FuraMono.otf);
}