@media print {
  .sections .page {
    width: 210mm;
    height: 297mm;
    display: flex;
    flex-flow: column;
  }
  .sections .page > * {
    flex: auto;
  }
  .sections .page > .flex-0 {
    flex: 0;
  }
}
.sections .part {
  font-size: 2rem;
  border-top: 1em solid #101011;
  padding: 1rem 1rem 6rem 1rem;
  page-break-inside: avoid;
}
.sections .part:hover article {
  opacity: 0.3;
}
.sections .part:hover article:hover {
  opacity: 1;
}
.sections .part h2 {
  font-size: 3rem;
  transition: color 1s;
  text-align: center;
}
.sections .part article {
  padding: 0 30rem 0 30rem;
  transition: opacity 0.3s;
  opacity: 1;
}
.sections .part article h3 {
  margin-bottom: 0;
  font-size: 2rem;
  font-weight: normal;
}
.sections .part article h3 .project {
  font-weight: bold;
}
.sections .part article h3 .provider {
  transition: color 0.2s;
}
.sections .part article .floating-container {
  width: 20rem;
  transform: rotate(2deg);
  opacity: 0;
  position: absolute;
  margin-top: -1.5rem;
  margin-left: calc(0rem - 20rem - 5rem);
  font-family: serif;
  white-space: nowrap;
  text-align: right;
}
@media print {
  .sections .part article .floating-container {
    float: right;
  }
}
.sections .part article .floating-container .date {
  font-size: 1.5rem;
  display: block;
  transform: rotate(2deg);
}
.sections .part article .floating-container .link {
  padding: 1.5rem;
  font-size: 4rem;
  display: inline-block;
  transform: rotate(-10deg);
}
@media print {
  .sections .part article .floating-container .link {
    display: none;
  }
}
@media screen and (max-width: 1200px) {
  .sections .part article {
    padding: 0 1rem 0 1rem;
  }
  .sections .part article .floating-container {
    opacity: 1;
    width: auto;
    transform: none;
    position: relative;
    margin-top: 0;
    margin-left: 0;
    text-align: left;
  }
  .sections .part article .floating-container .date,
.sections .part article .floating-container .link {
    transform: none;
  }
}
@media print {
  .sections .part article {
    padding: 0 1rem 0 1rem;
  }
  .sections .part article .floating-container {
    opacity: 1;
    width: auto;
    transform: none;
    position: relative;
    margin-top: 0;
    margin-left: 0;
    text-align: left;
  }
  .sections .part article .floating-container .date,
.sections .part article .floating-container .link {
    transform: none;
  }
}
.sections .part article:hover {
  opacity: 1;
}
.sections .part article:hover .floating-container {
  opacity: 1;
}
.sections .part article:hover .floating-container .link {
  animation: github 1s linear infinite;
}
@keyframes github {
  0% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(-370deg);
  }
}
.sections .part article .skills span {
  font-weight: bold;
  display: inline-block;
  font-size: 1rem;
  padding: 0.23em 1em;
  border-radius: 0.3em;
  margin-right: 0.3em;
  margin-bottom: 0.3em;
  transition: background-color 0.1s;
}
.sections .part article .description p {
  margin-top: 0.1em;
  font-size: 1.5rem;
}
.sections .part.primary {
  background: #222325 url(../images/noise.png);
  color: #fff;
}
.sections .part.primary article:hover .skills span {
  background: #f76868;
}
.sections .part.primary article:hover h2 .provider {
  color: #f76868;
}
.sections .part.primary h2 {
  color: #fff;
}
.sections .part.primary:hover h2 {
  color: #f76868;
}
.sections .part.primary .date,
.sections .part.primary .link {
  color: #fff;
}
.sections .part.primary article .skills span {
  background: #00000030;
  color: #fff;
}
@media print {
  .sections .part.primary .skills span {
    background: #f76868 !important;
  }
  .sections .part.primary h2 .provider {
    color: #f76868 !important;
  }
}
.sections .part.secondary {
  background: #efefef;
  color: #000;
}
.sections .part.secondary article:hover .skills span {
  background: #f76868;
}
.sections .part.secondary article:hover h2 .provider {
  color: #f76868;
}
.sections .part.secondary h2 {
  color: #000;
}
.sections .part.secondary:hover h2 {
  color: #f76868;
}
.sections .part.secondary .date,
.sections .part.secondary .link {
  color: #000;
}
.sections .part.secondary article .skills span {
  background: #00000030;
  color: #000;
}
@media print {
  .sections .part.secondary .skills span {
    background: #f76868 !important;
  }
  .sections .part.secondary h2 .provider {
    color: #f76868 !important;
  }
}